import { CButton } from '@coreui/react';
import {React, useState, useEffect} from 'react';
import {
CAlert,
CFormSelect,
CFormLabel,
CFormControl,

} from '@coreui/react'
import {api_url,axios} from '../../config'
import DatePicker from "react-datepicker";
const EditLeague= (props) => {

    const league_id = props.match.params.id;

    //console.log("editprops",props.location.editProps.data.winning_amount)

    const [dataEdit, setdataEdit] = useState({

        league_name: '',
        game_id: '',
        start_date:'',
        end_date:'',
        start_time:'',
        end_time:'',
        entry_fee:'',
        // min_player:'',
        max_player:'',
        league_details:'',
        track_type:'',
      
        prize_distribution_note:'',
        jackpot_note:'',
        league_image_elem:'',
        // league_availibility:'',
        level_id:'',
        // platform_commission_percentage:'',
        // winning_amount:'',
        // mode:'',
        no_of_laps:''
    })
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(new Date());
    const [status, setStatus] = useState(0)
   
    useEffect(() => {
       
        editDataApi();
    },[])

    useEffect(() => {
        console.log(status)
        console.log(dataEdit)
    }, [status, dataEdit])
    
    const editDataApi = () => {
        // const id = props.location.editProps.data.league_id;
        axios.get( api_url + 'admin/leagues/'+ league_id,{
            "league_id": league_id
        
        },{
            Headers: {
                'Content-Type': 'application/json',
                 'Authorization': 'Bearer '+localStorage.getItem('token')
            },
        } ).then(result => {
            console.log(result)
            let data = result.data.data[0]
            console.log("VIEW",data.league_image)
            setdataEdit(data);
        })
    }

    const publishStatus = () => {
        // const status = props.location.editProps.data.status
        const id = props.location.editProps.data.league_id;
        axios.put( api_url + 'admin/leagues-status/'+ id,{
            headers: {
                'Content-Type': 'application/json',
                 authorization: 'Bearer '+localStorage.getItem('token')
            }
        } ).then(result => {
            console.log(result)
            setStatus( result.data.status)
        })
    }

// eslint-disable-next-line
Date.prototype.getUnixTime = function() { return this.getTime()/1000|0 };
var FormData = require('form-data');
const edit_league = ()=>{
    document.getElementById('save-button').innerHTML = 'Loading ...';
    
    let league_name = document.getElementById('league_name').value;
    let game_id = document.getElementById('game_id').value;

    // Fixme start and enddate needs convertion to unix timestamp
    let start_date = document.getElementById('start_date').value;
    let end_date = document.getElementById('end_date').value;
    let start_time = document.getElementById('start_time').value;
    let end_time = document.getElementById('end_time').value;

    const start_datetime = new Date(start_date+' '+start_time).getTime() / 1000
    const end_datetime = new Date(end_date+' '+end_time).getTime() / 1000

    console.log('end_datetime:',end_datetime)
    let entry_fee = document.getElementById('entry_fee').value;
    // let min_player = document.getElementById('min_player').value;
    let max_player = document.getElementById('max_player').value;
    let league_details = document.getElementById('league_details').value;

    let track_type = document.getElementById('track_type').value;

    // let status = document.getElementById('status').value;

    let prize_distribution_note = document.getElementById('prize_distribution_note').value;

    let jackpot_note = document.getElementById('jackpot_note').value;

    const league_image_elem = document.getElementById('league_image')
    const league_image = league_image_elem.files[0]
    //const imageUrl = URL.createObjectURL(league_image)
    // let league_availibility = document.getElementById('league_availibility').value;

    let level_id = document.getElementById('level_id').value;

    // let platform_commission_percentage = document.getElementById('platform_commission_percentage').value;
    

    // let winning_amount = document.getElementById('winning_amount').value;
   

    // let mode = document.getElementById('mode').value;
    

    let no_of_laps = document.getElementById('no_of_laps').value;
    
    // if (error_flag === 1){
    //     show_error()
    // }
    // else{
        var data = new FormData();
        data.append('league_image', league_image, league_image)    
        data.append('game_id', game_id);
        // data.append('league_availibility', league_availibility);
        data.append('start_date',start_datetime);
        data.append('end_date',end_datetime)
        data.append('start_time', start_datetime);
        data.append('end_time', end_datetime);
        data.append('level_id', level_id);
        data.append('entry_fee', entry_fee);
        // data.append('total_prize_value', '1');
        // data.append('platform_commission_percentage', platform_commission_percentage);
        // data.append('winning_amount', winning_amount);
        data.append('max_player', max_player);
        // data.append('min_player', min_player);
        // data.append('admin_earning', '4');
        data.append('league_details', league_details);
        data.append('track_type', track_type);
        // data.append('mode', mode);
        data.append('status', status);
        data.append('prize_distribution_note', prize_distribution_note);
        data.append('jackpot_note', jackpot_note);
        data.append('league_name', league_name);
        data.append('no_of_laps', no_of_laps);
        var config = {
            method: 'patch',
            url: api_url+'admin/leagues/'+league_id,
            data : data
          };
          console.log("Edit Data",data)
          
        axios(config).then((resp)=>{
            document.getElementById('save-button').innerHTML = 'Save';
            if (resp.status === 200){
                
                document.getElementById('league-add-form').reset()
                document.getElementById('form-error').style.display='none';
                document.getElementById('form-success').style.display='block';
                document.getElementById('form-success').innerHTML = 'League has been added successfully!';
                window.location.href="/#/leagues"
            }
            else if(resp.status === 500){
                document.getElementById('form-error').style.display='block';
                document.getElementById('form-error').innerHTML = resp.data
            }
            else if(resp.status === 401){
                console.log("JWT needs to be refreshed")
            }
        })
    // }

} 

    const timeChange = (e) => {

        let newEdit = { ...dataEdit };
    newEdit[e.target.id] = e.target.value;
    setdataEdit(newEdit);

        // console.log(e)
        // const {target: {name, value}} = e;
    }
    // const gameTypeChange = (e) => {
    //     let newEdit = { ...dataEdit };
    //     newEdit[e.target.id] = e.target.value;
    //     setdataEdit(newEdit);
       
    // }
return (
<>

    <div className='container'>
        <div className='row'>
            <div className='col-12'>
                <div className='card p-3'>
                    <form id='league-add-form'>
                        <div className='row'>
                            <div className='col-12'>
                                <h3 className='float-start'><b>Edit League Details</b></h3>
                                
                                <div className='float-end'><CButton id='save-button'  onClick={()=>{publishStatus()}}  color={!status ? "success":"danger"} >{!status ? "Publish" : "Unpublish"}</CButton></div>
                                
                            </div>
                        </div>
                        <hr />

                        <div className="mb-3">
                            <CFormLabel ><b>League Name*</b></CFormLabel>
                            <CFormControl type="text" id="league_name" onChange={timeChange} value={dataEdit.league_name} placeholder="Enter League Name" required />
                        </div>

                        <div className="mb-3">
                            <CFormLabel ><b>League Image*</b></CFormLabel>
                            <CFormControl type="file" id="league_image"   />
                            <img id="blah" src="#" alt="your image" />
                        </div>

                        <div className="mb-3">
                            <CFormLabel ><b>Game*</b></CFormLabel>
                            <CFormSelect value= {dataEdit.game_id} id='game_id'  onChange={timeChange} required>
                                
                                <option value="0">Select Game</option>
                                <option value="1">3D Car Racing</option>

                            </CFormSelect>
                        </div>
                        <div className="mb-3">
                            <CFormLabel ><b>Track Type*</b></CFormLabel>
                            <CFormSelect onChange={timeChange} value={dataEdit.track_type} id='track_type' required>
                                <option value="0">Select Track Type</option>
                                <option value="new_york_blitz">new_york_blitz</option>
                                <option value="track_2">track_2</option>

                            </CFormSelect>
                        </div>
                        <div className="mb-3">
                            <CFormLabel ><b>Level*</b></CFormLabel>
                            <CFormSelect onChange={timeChange} value={dataEdit.level_id} id='level_id' required>
                                <option value="0">Select Level</option>
                                <option value="1">Easy</option>
                                <option value="2">Medium</option>
                                <option value="3">Hard</option>

                            </CFormSelect>
                        </div>
                        {/* <div className="mb-3">
                            <CFormLabel ><b>League Availibility*</b></CFormLabel>
                            <CFormSelect onChange={timeChange} value={dataEdit.league_availibility} id='league_availibility' required>
                                <option value="0">Select League Availibility</option>
                                <option value="fixed">Fixed</option>
                                <option value="variable">Variable</option>

                            </CFormSelect>
                        </div> */}
                        {/* <div className="mb-3">
                            <CFormLabel ><b>Mode*</b></CFormLabel>
                            <CFormSelect onChange={timeChange} value={dataEdit.mode} id='mode' required>
                                <option value="0">Select Mode</option>
                                <option value="day">Day</option>
                                <option value="night">Night</option>

                            </CFormSelect>
                        </div> */}

                        <div className="row mb-3">
                            <div className='col-6'>
                                <CFormLabel htmlFor="exampleFormControlInput1"><b>Start Date*</b></CFormLabel>
                                {/* <CFormControl type="date" value={dataEdit.startDate} id="start_date" required/> */}
                                <DatePicker type="date" selected={startDate}  value={dataEdit.startDate} onChange={(date) => setStartDate(date)} style={{width:"500px"}} minDate={new Date()} id="start_date" required/>
                            </div>
                            <div className='col-6'>
                                <CFormLabel ><b>End Date*</b></CFormLabel>
                                {/* <CFormControl type="date" value={dataEdit.endDate} id="end_date" required  /> */}
                                <DatePicker type="date" selected={endDate} onChange={(date) => setEndDate(date)}  id="end_date" required/>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <div className='col-6'>
                                <CFormLabel htmlFor="exampleFormControlInput1"><b>Start Time*</b></CFormLabel>
                                <CFormControl type="time" value={dataEdit.start_datetime} onChange={timeChange} id="start_time" required/>
                                
                            </div>
                            <div className='col-6'>
                                <CFormLabel ><b>End Time*</b></CFormLabel>
                                <CFormControl type="time" id="end_time" value = {dataEdit.end_time} required />
                            </div>
                        </div>

                        <div className="row mb-3">
                            <div className='col-4'>
                                <CFormLabel ><b>Entry Fee*</b></CFormLabel>
                                <CFormControl min="0" type="number" id="entry_fee" onChange={timeChange} value = {dataEdit.entry_fee} placeholder="Rs. 00 (Don't Enter Rs.)" />
                            </div>
                            <div className='col-4'>
                                <CFormLabel ><b>No of Laps*</b></CFormLabel>
                                <CFormControl min="1" type="number" id="no_of_laps" onChange={timeChange} value = {dataEdit.no_of_laps} placeholder="no_of_laps of Players" />
                            </div>
                            {/* <div className='col-4'>
                                <CFormLabel ><b>Platform Commission Percentage*</b></CFormLabel>
                                <CFormControl min="1" type="number" onChange={timeChange} id="platform_commission_percentage" value = {dataEdit.platform_commission_percentage} placeholder="platform_commission_percentage" />
                            </div> */}
                            {/* <div className='col-4'>
                                <CFormLabel ><b>Winning Amount*</b></CFormLabel>
                                <CFormControl min="1" type="number" onChange={timeChange} id="winning_amount" value = {dataEdit.winning_amount} placeholder="winning amount of League" />
                            </div> */}
                            
                            {/* <div className='col-4'>
                                <CFormLabel ><b>Min. No. of Players*</b></CFormLabel>
                                <CFormControl min="1" type="number" onChange={timeChange} id="min_player" value = {dataEdit.min_player} placeholder="Minimum Number of Players" />
                            </div> */}
                            <div className='col-4'>
                                <CFormLabel ><b>Max. No. of Players*</b></CFormLabel>
                                <CFormControl min="1" type="number" onChange={timeChange} id="max_player" value = {dataEdit.max_player} placeholder="Maximum Number of Players" />
                            </div>

                        </div>
                        <h3><b>League Notes</b></h3>
                        <hr />

                        <div className="mb-3">

                            <CFormLabel ><b>League Details</b></CFormLabel>
                            <CFormControl component="textarea" required id="league_details" onChange={timeChange} value = {dataEdit.league_details} rows="3"></CFormControl>
                        </div>

                        <div className="mb-3">
                            <CFormLabel ><b>Prize Distribution Note</b></CFormLabel>
                            <CFormControl component="textarea"  required id="prize_distribution_note" onChange={timeChange} value = {dataEdit.prize_distribution_note} rows="3"></CFormControl>
                        </div>

                        <div className="mb-3">
                            <CFormLabel ><b>Jackpot Note</b></CFormLabel>
                            <CFormControl component="textarea" required id="jackpot_note" onChange={timeChange} value = {dataEdit.jackpot_note} rows="3"></CFormControl>
                        </div>
                        <div className="row mb-3">
                            <div className="d-grid gap-2 d-md-block">
                            <CButton id='save-button'  onClick={edit_league}  color="success" >Save League</CButton>
                                
                            </div>
                            
                        </div>
                        

                        <CAlert color="danger" id='form-error' style={{'display':'none'}}></CAlert>
                        <CAlert color="success" id='form-success' style={{'display':'none'}}></CAlert>
                        </form>

                </div>

            </div>
        </div>
    </div>

</>
)
}


export default EditLeague